import { Component, OnInit } from '@angular/core';
import { Subscription, Observable, Observer  } from 'rxjs';
import { ApiService } from 'src/app/common/api-service/api.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-d1-list-company',
  templateUrl: './d1-list-company.component.html',
  styleUrls: ['./d1-list-company.component.scss']
})
export class D1ListCompanyComponent implements OnInit {
  /** for table */
  subscription: Subscription[] = [];

  newestCompanies: any=[];
  mostVistedCompanies: any=[];
  companies: any=[];
  cities: any=[];
  careeres: any=[];
  cityId = '';
  careerId = '';
  companyName='';

  observable: Observable<any>;  
  observer: Observer<any>;
  count=0;


  /**
   * constructor
   * @param api 
   */
  constructor(private api: ApiService,
    private router: Router, private activatedRoute: ActivatedRoute) {
    // add validate for controls
    
    // xử lý bất đồng bộ  
    this.observable = Observable.create((observer: any) => {  
      this.observer = observer; 
    });
  }

  ngOnInit(): void {
    this.observable.subscribe(done=>{
      if(done==4){
        this.activatedRoute.params.subscribe(params => {
          this.companyName = params.keyword;
          this.careerId = params.career;
          this.cityId = params.city;
        });
      }
    })
    
    this.onLoadDataCity();
    this.onLoadDataCareer();
    this.onLoadNewestCompanyData();
    this.onLoadMostVistedCompanyData();
  }

  /**
   * onLoadDataCity
   */
  onLoadDataCity(){
    const param={};
     this.subscription.push(this.api.excuteAllByWhat(param, '63').subscribe(data => {

       if (data.length>0) {
        let temp = [{
          id : '0',
          name : 'Địa điểm'
        }];
        data.forEach(item => {
          temp.push(item);
        })
        this.cities = temp;

         // set first select city combobox
        this.cityId = this.cities[0].id;  
        this.count++;
        this.observer.next(this.count);
      }
     }));
  }

  /**
   * onLoadDataCareer
   */
  onLoadDataCareer(){
    const param={};
     this.subscription.push(this.api.excuteAllByWhat(param, '64').subscribe(data => {

       if (data.length>0) {
        let temp = [{
          id : '0',
          name : 'Ngành nghề'
        }];
        data.forEach(item => {
          temp.push(item);
        })
        this.careeres = temp;

         // set first select career combobox
        this.careerId = this.careeres[0].id;  
        
        this.count++;
        this.observer.next(this.count);
      }
     }));
  }

  /**
   * onFillterClick
   */
  isSearch = false;
  countCompany = 0;
  onFilterClick(){
    this.isSearch = true;
    this.countCompany = 0;
    const param={"companyname": this.companyName, "city": this.cityId, "career": this.careerId};
     this.subscription.push(this.api.excuteAllByWhat(param, '66').subscribe(data => {
      if (data.length > 0) {
        
        // get number of company have been finded
        data.forEach(element => {
          this.countCompany = this.countCompany + 1;
        });

        // set data for table
        this.companies = data;
      }
      else {
        // set data for table
        this.companies =[];
      }
     }));
   }

   
   /**
    * onLoadNewestCompanyData
    */
  onLoadNewestCompanyData(){
    const param={};
     this.subscription.push(this.api.excuteAllByWhat(param, '62').subscribe(data => {
      if (data.length>0) {
        
        let colSlide = [];
        let processList = [
          {
              rowSlide : [
                colSlide = [data[0]],
                colSlide = [data[1]],
                colSlide = [data[2]],
                colSlide = [data[3]],
            ],
            active: true
          },
          {
            rowSlide : [
                colSlide = [data[4]],
                colSlide = [data[5]],
                colSlide = [data[6]],
                colSlide = [data[7]],
                ],
            active: false
          }
        ];
        this.newestCompanies = processList;
        this.count++;
        this.observer.next(this.count);
      }
     }));
  }

  
   onLoadMostVistedCompanyData(){
    const param={};
     this.subscription.push(this.api.excuteAllByWhat(param, '67').subscribe(data => {
      if (data.length>0) {
        
        let colSlide = [];
        let processList = [
          {
              rowSlide : [
                colSlide = [data[0]],
                colSlide = [data[1]],
                colSlide = [data[2]],
                colSlide = [data[3]],
            ],
            active: true
          },
          {
            rowSlide : [
                colSlide = [data[4]],
                colSlide = [data[5]],
                colSlide = [data[6]],
                colSlide = [data[7]],
                ],
            active: false
          }
        ];
        this.mostVistedCompanies = processList;

        this.count++;
        this.observer.next(this.count);
      }
     }));
  }

  /**
   * on Company Click
   */
  onCompanyClick(id){ 
    const url = '/d2-detail-company/' + id;
    this.router.navigate([url]);
  }

}
